.subscription {

  &__content {
    @include pageGrid;

    align-content: center;

    gap: 0;
  }

  &__title {
    padding-top: 188px;

    grid-column: span 2;

    @include onTablet {
      grid-column: span 6;
    }
    @include onDesktop {
      grid-column: 4/ span 6;
    }

    color: #fff;
  }

  &__subtitle {
    font-family: "IBM Plex Sans";
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    text-align: center;

    grid-column: span 2;

    color: #fff;

    @include onTablet {
      grid-column: span 6;
    }

    @include onDesktop {
      grid-column: 4/ span 6;
    }
  }

  &__form {
    padding-top: 50px;

    display: flex;

    height: 50px;

    grid-column: span 2;

    @include onTablet {
      grid-column: 2/ span 4;
      gap: 16px;
    }
    @include onDesktop {
      grid-column: 4/ span 6;
      gap: 30px;
    }
  }

  &__input {
    width: 80%;
    padding: 0;
    margin: 0;
    border: 0px solid white;
    box-sizing: border-box;
    height: 50px;
    padding-left: 32px;

    @include onTablet {
      height: 70px;
      width: 87%;
    }

    &:focus {
      background-color: #fff;
      resize: none;
      padding: 0;
      margin: 0;
      padding-left: 32px;

    }

    &::placeholder {
      font-family: "Montserrat";
      font-size: 14px;
      font-weight: 400;
      line-height: 100%;
    }
  }

  &__button {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    text-decoration: none;

    background-color: #F14A27;

    width: 20%;

    border: none;

    @include onTablet {
       height: 70px;

       width: 13%;
    }
  }

  &__bg {

  position: absolute;

  z-index: -1;
  width: 100%;
  height: 386px;
  background-image: url(../images/subscription.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  margin-top: 100px;

    @include onTablet {
      height: 380px;
    }
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus {
    -webkit-text-fill-color: #0F0E08;
    -webkit-box-shadow: 0 0 0px 40rem #fff inset;
  }

}
